/* General */

body {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

/* Main  */

.main {
    background-color: #f1f7fb;
    min-height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container {
    width: 70vw;
    max-width: 70vw;
    display: flex;
    flex-wrap: wrap;
}

/* Header  */

.header {
    margin: 100px 0 30px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    margin: 16px 0;
    font-size: 42px;
}

.line {
    width: 5vw;
    height: 4px;
    background-color: #bc9b61;
}

.navbar {
    width: 500px;
}

.navbar .list {
    display: flex;
    justify-content: space-evenly;
}

.list .list-item {
    list-style: none;
    margin: 4px 5px;
    padding: 8px 15px;
    font-size: 18px;
    font-weight: 400;
    color: #bc9b61;
    border-radius: 3px;
    cursor: pointer;
    transition: all 0.15s ease;
}

.list .list-item:hover {
    background-color: #bc9b61;
    color: #f1f7fb;
}

/* Menu  */

.menu {
    width: 100%;
    display: flex
}

/* Card  */

.card {
    display: flex;
    width: 50%;
    max-width: 50%;
    padding: 20px 12px;
    box-sizing: border-box;
}

.img-container {
    width: 700px;
    height: 160px;
    border: 4px solid #bc9b61;
    border-radius: 3px;
}

.img-container img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.info-container {
    padding-left: 20px;
}

.info-container .top {
    display: flex;
    align-items: center;
    padding:  0 0 5px 0;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: #bc9b61;

}

.top .card-title {
    margin: 0;
    font-size: 17px;
    font-weight: 600;
    letter-spacing: 0.15rem;
}

.top .price {
    margin: 0 0 0 auto;
    font-size: 19px;
    font-weight: 700;
    letter-spacing: 0.15rem;
    color: #bc9b61;
}

.desc {
    color: #899291;
}




